<template>
  <div id="pageheader">
      <div class="top">
        <el-container direction="horizontal">
          <el-main style="margin-top: 10px">  </el-main>
          <el-main>
            <ul>
              <li><span @click="toIndex"></span></li>
            <!--  <li></li>-->
            <!--  <li> <span @click="clearCookie">test</span></li>-->
              <li> <span @click="toRegister"></span></li>
              <li><template v-if="!this.$store.state.token"><span @click="loginCheck()"></span></template></li>
              <li class="img-no-li"><template v-if="this.$store.state.token" class="img-down">
                <el-dropdown>
                  <span class="el-dropdown-link">
                    <el-avatar :src="this.$store.state.user.userImg" style="border:1px solid #EEEAA0"></el-avatar>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item icon="el-icon-user">
                      <span></span>
                    </el-dropdown-item>
                    <el-dropdown-item icon="el-icon-user">
                      <span @click="toaddbook"></span>
                    </el-dropdown-item>
                    <el-dropdown-item icon="el-icon-user">
                      <span @click="toshoppingcar"></span>
                    </el-dropdown-item>
                    <el-dropdown-item icon="el-icon-circle-close">
                      <span @click="toorderlist"></span>
                    </el-dropdown-item>
                    <el-dropdown-item icon="el-icon-circle-close">
                      <span @click="logout"></span>
                    </el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
               </template> </li>

            </ul>
          </el-main>
        </el-container>
        <!--Dialog:append-to-body='true':lock-scroll="false"dialogwidth="40%"-->
        <el-dialog title="" :visible.sync="dialogFormVisible"  center :append-to-body='true' :lock-scroll="false" width="30%">
          <!---->
          <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
            <el-form-item label="   " prop="num">
              <el-input v-model.number="ruleForm2.account" style="width:250px"></el-input>
            </el-form-item>

            <el-form-item label="   " prop="pass">
              <el-input type="password" v-model="ruleForm2.password" auto-complete="off" style="width:250px"></el-input>
            </el-form-item>

            <el-form-item label="" prop="delivery" class="from-switch">
              <el-switch v-model="delivery" active-color="#E0B41B"></el-switch>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false,resetForm('ruleForm2')"> </el-button>
            <el-button type="primary" @click="submitForm('ruleForm2')"> </el-button>
          </div>
        </el-dialog>
      </div>


  </div>
</template>
<script>
  import {login} from '../axios/api'
  export default {
    name: "pageheader",
    data() {
      /*form*/
      var checkNum = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error(''));
          } else {
            var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
            if (!myreg.test(value) ) {
              callback(new Error(''));
            } else {
              callback();
            }

          }
        }, 1000);
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {

          callback();
        }
      };
      /*form*/

      return {
        userImgURl:'',
        delivery: false,
        /*form*/
        /**/
        ruleForm2: {
          password: '',
          account: '',

        },
        rules2: {
          password: [
            { validator: validatePass, trigger: 'blur' }
          ],

          account: [
            { validator: checkNum, trigger: 'blur' }
          ]
        },

        /*form*/
        dialogFormVisible:false,
        dialogTableVisible: false,
        form: {
          name: '',
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px'
      };
    },
    //cookie
    mounted() {
      this.getCookie();
    },
    methods: {
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            login(this.ruleForm2).then(res=>{
              if(res.data!=""){
                // cookie
                console.log(res.data);
                if (this.delivery == true) {
                  console.log("checked == true");
                  //3
                  this.setCookie(this.ruleForm2.account, this.ruleForm2.password, 7);
                } else {
                  console.log("Cookie");
                  //Cookie
                  this.clearCookie();
                }
                this.$store.commit('setToken',true);
                this.dialogFormVisible = false;
                this.userImgURl="http://localhost:88/upload/"+res.data.userImg;
                res.data.userImg=this.userImgURl;
                this.$store.commit('setUser',res.data);
                console.log(this.userImgURl)
                console.log(res.data)
                this.$message({
                  type: 'success',
                  message: ''+this.$store.state.user.userName,
                  showClose: true,
                });
              }else{
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'error'
                });
                this.ruleForm2.account="";
                this.ruleForm2.password="";
                this.delivery=false;
              }
            })
          } else {
            this.$message.error("");
            return false;
          }
        });
      },
      //cookie
      setCookie(c_name, c_pwd, exdays) {
        var exdate = new Date(); //
        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //
        //cookie
        window.document.cookie = "account" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
        window.document.cookie = "password" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
      },
      //cookie
      getCookie: function () {
        if (document.cookie.length > 0) {
          var arr = document.cookie.split('; '); //
          for (var i = 0; i < arr.length; i++) {
            var arr2 = arr[i].split('='); //
            //
            if (arr2[0] == 'account') {
              this.ruleForm2.account = arr2[1]; //
            } else if (arr2[0] == 'password') {
              this.ruleForm2.password = arr2[1];
            }
          }
        }
      },
      //cookie
      clearCookie: function () {
        this.setCookie("account", "password", -1); //21
        this.$store.commit('setToken',false);
      },
      loginCheck: function () {
        if (this.$store.state.token==true) {
          this.$message({
            showClose: true,
            message: ''
          });
        } else {
          this.dialogFormVisible = true;
        }
      },
      logout: function () {
        if (document.cookie.length > 0) {
          this.clearCookie();
          this.$message({
            showClose: true,
            message: '',
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: '',
          });
        }

      },
      toshoppingcar(){
        this.$router.push('/shoppingcar')
      },

      toRegister(){
        this.$router.push('/register')
      },
      toIndex(){
        this.$router.push('/')
      },
      toaddbook(){
        this.$router.push('/addbookbyuser')
      },
      toorderlist(){
        this.$router.push('/orderlist')
      }
    }
  }
</script>

<style scoped>
  .dialog-footer >>>.el-button--primary{
    background-color: #E0B41B;
    border: 0;

  }
  .dialog-footer >>> .el-button{
    width: 120px;
  }

#pageheader{
  width: 100%;
  background-color: #666752;
  color: white;
  height: 45px;
}
  .top{
    width: 1226px;
    margin:0 auto;
  }
  ul{
    list-style-type:none;
    margin:0px;
    width: 200px;
    position: relative;
    left:360px;
  }
  ul li{
    float: left;
    margin-left: 8px;
    margin-top:10px;
  }
  ul > .img-no-li{
    margin-top: 1px;
  }
</style>



